//------------------------------------*\
//  $NAVIGATION
//------------------------------------*/

// Pagination
.a-pagination {
  .page-item {
    .page-link {
      padding: 5px 14px 3px;
      margin-right: 12px;
      margin-bottom: 12px;
      color: $black;
      background-color: transparent;
      border: 1px solid $white;
      border-radius: 0;

      &:focus,
      &:hover {
        color: $white;
        text-decoration: none;
        background-color: $blue;
        border-color: $blue;
      }
    }

    &.active {
      .page-link,
      .page-link {
        &:hover {
          color: $black;
          background-color: $white;
          border: 1px solid $white;
        }
      }
    }
  }
}

// Change colors on Pagination when different bacgrounds on body
.a-bgGreyLight,
.a-bgBlueLight {
  .page-item {
    .page-link {
      background-color: $white;
    }

    &.active {
      .page-link {
        border: 2px solid $white;

        &:hover {
          border: 2px solid $white;
        }
      }
    }
  }
}

// Breadcrumb
.a-breadcrumb {
  padding: 0;
  font-size: 20px;
  background: transparent;

  li {
    display: none;

    line-height: $spacer * 3;

    @include media-breakpoint-up( md ) {
      display: block;
    }

    &.breadcrumb-item + .breadcrumb-item {
      &::before {
        padding-right: 0;
        padding-left: 0;

        @include media-breakpoint-up( sm ) {
          padding-right: $spacer / 2;
          padding-left: $spacer;
        }
      }
    }

    &.breadcrumb-mobile {
      @include media-breakpoint-down( sm ) {
        display: block;

        &::before {
          display: inline-block;
          padding-right: 0;
          padding-bottom: 2px;
          font: normal normal normal 14px/1 'AltinnNo';
          font-size: inherit;
          color: $black;

          content: '\f00a';
          transform: rotate( 180deg );

          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale;
          text-rendering: auto;
        }

        .a-link {
          margin-left: $spacer / 2;
        }
      }
    }

    &.active {
      a {
        color: $grey;
        cursor: default;
        border: none;
      }
    }
  }
}

// Dropdown-menu with arrow
.a-dropdownTriangle {
  box-sizing: border-box;

  background: $white;
  box-shadow: 2px 2px 9px 0 $black-opacity-04;

  &::after {
    position: absolute;
    top: 1px;
    bottom: 100%;
    left: 50%;
    box-sizing: border-box;
    width: 0;
    height: 0;
    margin-left: 130px;

    content: '';
    border: 8px solid $black;
    border-color: transparent transparent $white $white;
    box-shadow: -2px 2px 3px 0 $black-opacity-01;
    transform: rotate(135deg);
    transform-origin: 0 0;
  }
}


//------------------------------------*\
//  Small accordion
//------------------------------------*/

.a-accordion-small {
  a {
    padding-bottom: 15px;
  }

  div {
    padding-left: 20px;
  }

  .a-dropdownArrow {
    margin-right: 10px;
  }
}


/*------------------------------------*\
    Large accordion
\*------------------------------------*/
.a-accordion-large {
  border-bottom: 1px solid $blue-line;

  .a-collapse-title {
    @include media-breakpoint-up( md ) {
      padding-left: $spacer * 4;
    }

    display: flex;
    min-height: $spacer * 6;

    border-top: 1px solid $blue-line;

    align-items: center;
    -webkit-box-align: center;
  }

  .a-link {
    display: inline;
  }
}


/* Top tasks */
.a-topTasks {
  @include media-breakpoint-up( xl ) {
    margin: 0 $spacer * 5 0 $spacer * 5;
	}

  // On mobile, show buttons with text below icon (centered)
  @include media-breakpoint-down( md ) {
    @include add-col-properties {
      padding-right: 1px;
      padding-left: 1px;
    }

    padding: 0 $spacer * 2;

    .a-btn-transparentWhite {
      height: 84px;
      padding: $spacer 0;

      text-align: center;

      .a-btn-icon-symbol {
        display: block;
        margin: auto;
        margin-bottom: 3px;
      }

      .a-btn-icon-text {
        padding-left: 0;
        font-size: 14px;
      }
		}
	}

  @include media-breakpoint-down( xs ) {
    padding: 0 0;

    .a-btn-transparentWhite {
      .a-btn-icon-text {
        font-size: 12px;
      }
    }
	}
}
