  }

  62.51% {
    transform: rotate( 270deg );
  }

  75% {
    transform: rotate( 360deg );
  }

  100% {
    transform: rotate( 360deg );
  }
}

@keyframes c-4-loop {
  0% {
    height: 40px;
    box-shadow: inset $b1 0 100px 0;
  }

  12.5% {
    height: $spacer * 5;
    margin-top: 0;
  }

  62.5% {
    height: $spacer * 5;
    margin-top: 0;
    box-shadow: inset $b1 0 100px 0;
  }

  75% {
    box-shadow: inset $b0 0 100px 0;
  }

  100% {
    height: 0;
    margin-top: 0;
    margin-bottom: 0;
  }
}

